import { API, graphqlOperation } from 'aws-amplify'
import { getConvo, getUserConversations, getEmployeeConversations } from '../graphql/queries'

import {
  createConvo,
  createConvoLink,
  updateConvoLink
} from '../graphql/mutations';

export const launchNewConversation = async(userId : string, expertid: string) => {
    try {
      const id = userId;
      const chat =  {  name: 'direct' };
  
      const conversation = await API.graphql(graphqlOperation(createConvo, { input: chat }))
      console.log("conversation", conversation);
  
      // const otherChatName = chatName(me.username)
      // const myChatName = chatName(user.username)
      // @ts-ignore 
      const owner = {convoLinkUserId: id, convoLinkConversationId: conversation.data.createConvo.id , 
        status: "CREATING", name: 'chat'};
      // @ts-ignore 
      const service = {convoLinkEmployeeId: expertid , convoLinkConversationId: conversation.data.createConvo.id, 
        status: "CREATING", name: 'chat'};
  
      const ownerConvLink = await API.graphql(graphqlOperation(createConvoLink, { input: owner }));
      const serviceConvLink = await API.graphql(graphqlOperation(createConvoLink, { input: service }));
      
          // @ts-ignore 
      const ownerConvLinkConf = ownerConvLink.data.createConvoLink.id;
          // @ts-ignore 
      const serviceConvLinkConf = serviceConvLink.data.createConvoLink.id
  
     const mm =  await API.graphql(graphqlOperation(updateConvoLink, {id : ownerConvLinkConf}));
     console.log(' mm next steps', mm)
     const xy =  await API.graphql(graphqlOperation(updateConvoLink, {id : serviceConvLinkConf}));
  
     console.log(' xy next steps', xy)

      // const links = await Promise.all([
      //   linkNewConversation(createConvo.id, user.id, otherChatName),
      //   linkNewConversation(createConvo.id, me.id, myChatName)
      // ])
      // console.log('next steps', links)
      // const promises = links.map(c => this.updateToReadyConversation(c))
      // const convoLinks = await Promise.all(promises)
  
  
      console.log('item created!',conversation)
    } catch (err) {
      console.log('error creating talk...', err)
    }
  }

  export const findConverationWithUser = async(ticketUserId : string, expertId: string, setIsEmployee: boolean) => {
    console.log('findConverationWithUser', ticketUserId, expertId, setIsEmployee)

    const op =  setIsEmployee ? getEmployeeConversations :getUserConversations;
    const id =  setIsEmployee ? expertId :ticketUserId;

    const commentData = await API.graphql(graphqlOperation(op, { id }))
    console.log('fetchDate',commentData); 

    if(setIsEmployee){

              //@ts-ignore
    if(commentData.data.getEmployee &&  commentData.data.getEmployee.employeeConversations.items.length > 0){
      //@ts-ignore
const convoLinks = commentData.data.getEmployee.employeeConversations.items as any;

const convoLink = convoLinks.find((c :any) => {
const {
conversation: { associated: { items: assoc = [] } = {} } = {}
} = c
return assoc.some((a :any)=> a.convoLinkUserId === ticketUserId)
})

return convoLink
? {
conversation: convoLink.conversation,
conversationName: convoLink.name
}
: null
}

    }else {
              //@ts-ignore
    if(commentData.data.getUser &&  commentData.data.getUser.userConversations.items.length > 0){
      //@ts-ignore
const convoLinks = commentData.data.getUser.userConversations.items as any;

const convoLink = convoLinks.find((c :any) => {
const {
conversation: { associated: { items: assoc = [] } = {} } = {}
} = c
return assoc.some((a :any)=> a.convoLinkEmployeeId === expertId)
})

return convoLink
? {
conversation: convoLink.conversation,
conversationName: convoLink.name
}
: null
}

    }

    return null;
  }


  export const startConvoWithUser = async (userId: string, expertId: string, setIsEmployee: boolean) => {
    let conversationInfo = await findConverationWithUser(userId, expertId, setIsEmployee);
    console.log('existing conv', conversationInfo)

    if (!conversationInfo) {
      console.log('no convo, launch new')
       await launchNewConversation(userId, expertId )
    }
    return conversationInfo

  }
