import React, { useContext } from 'react'
import { useHistory } from 'react-router-dom'
import {
  useTheme,
  createStyles,
  makeStyles,
  Theme
} from '@material-ui/core/styles'

import { Toolbar, AppBar, IconButton, Button } from '@material-ui/core'
import HomeIcon from '@material-ui/icons/Home'
import NotificationsIcon from '@material-ui/icons/Notifications';
import AddShoppingCartIcon from '@material-ui/icons/AddShoppingCart';
import { AuthContext } from '../auth/auth-context'
import ExitToAppIcon from '@material-ui/icons/ExitToApp';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    appBar: { boxShadow: 'none', backgroundColor: 'transparent' },
    appBarBefore: { backgroundColor: '#fff', border: 1 },

    menuButton: {
      marginRight: theme.spacing(1)
    },
    spacer: {
      flexGrow: 1
    }
  })
)

export interface HeaderProps {
  header?: string;
}

const Header: React.SFC<HeaderProps> = ({ header }) => {
  const classes = useStyles(useTheme())
  const authContext = useContext(AuthContext);
  const history = useHistory()

  const [openCart, setOpenCart] = React.useState(false);

  const buyButton = () => {
    setOpenCart(true)
  };

  const closeBuyButton = () => {
    setOpenCart(false)
  };

  let authLink = authContext.isAuthenticated() ? (
    <div>
      <IconButton aria-label="show 17 new notifications" color="inherit"
    className={classes.menuButton}
    onClick={() => history.push('/chat')}
>
    <NotificationsIcon color='action'/>
</IconButton>

{ header ?(<IconButton aria-label="show 17 new notifications" color="inherit"
    className={classes.menuButton}
    onClick={buyButton}
>
    <AddShoppingCartIcon color='action'/>
</IconButton>) : null}

    { <IconButton  color="inherit"
    className={classes.menuButton}
    onClick={() => authContext.signOut()}>
    <ExitToAppIcon color='action'/>
</IconButton> 
}
    </div>
  ) : null

  return (
    <AppBar position='fixed' className={authContext.isAuthenticated() ? classes.appBarBefore :classes.appBar}>
      <Toolbar>
        <IconButton
          edge='start'
          className={classes.menuButton}
          onClick={() => history.push('/')}
          color='inherit'
          aria-label='home'>
          <HomeIcon color='action' />
        </IconButton>
        <div className={classes.spacer}></div>
        {authLink}
      </Toolbar>
    </AppBar>
  )
}

export default Header
