export interface AddressFormProps {
  nextButton: (value: string) => void
}

  const CreateTicket: React.SFC<AddressFormProps> = (props) => {

    const classes = useStyles();

  const handleInputChange = (e: any) => {
    const {name, value} = e.target
    if(name == "price"){
      const re = /^([1-9]{0,2}((.)[0-9]{0,2}))$/;
      if (e.target.value === '' || re.test(e.target.value)) {
        setValues({...values, [name]: value})
      }
    }else {
      setValues({...values, [name]: value})

    }
}
const history = useHistory()
const authContext = React.useContext(AuthContext)


const createNewTicket = async() =>{
  console.log("createNewTicket", values);
  const id = authContext.userId;

  const phone_number = authContext.user.attributes["phone_number"];
  const email = authContext.user.attributes["email"];
  const ticketUserInfo = JSON.stringify({
    email: email,
    phoneNumber: phone_number
  });
  let S3path: any[] = [];
  if(pictures.length > 0){
     S3path = await handleImageUpload(pictures); 
  }

  const newId= uuid();
  //const county = "Davidson County";

  const response =  await API.graphql(
    graphqlOperation(createTicket, { input: {
      id: newId,
      countAndId: `${values.county}_${newId}`,
      price : values.price,
      time : values.time,
      daytime: values.daytime,
      title : values.title,
      content:  values.content ,
      destination : values.destination? values.destination: values.source,
      source : values.source ?? "Davidson County" ,
      county: values.county,
      ownerUploadImages : JSON.stringify(S3path),
      ticketUserId: id,
      status: 'open',
      ticketUserInfo: ticketUserInfo
    } })
);
console.log("response", response);

history.push('/')

}
const sourceGoogle = (result: GeocoderResult[]) => {
  console.log("address",result);
  const value = stripAddress(result);
  setValues({...values, source: value.formatted_address, county: value.county})
};

const destGoogle = (result: GeocoderResult[]) => {
  console.log("address",result);
  const value = stripAddress(result);
  setValues({...values, destination: value.formatted_address})
};

 const [values, setValues] = useState({ content: '',price: '', title: '',
 source: '', destination: '',time: "", county: '', daytime: '', terms: false })

 const [dayTime, setDayTime] = useState({ hour: '',min: '', non: ''});

 const handleDayTimeChange = (e: any) => {
  const {name, value} = e.target
  console.log("handleDayTimeChange",name, value);

    setDayTime({...dayTime, [name]: value})
}

useEffect(() => {
  setValues({...values, ["daytime"]: `${dayTime.hour}:${dayTime.min}:${dayTime.non}`})
}, [dayTime ]);

  const [disable, setDisable] = useState<boolean>(true)

  useEffect(() => {
    const {content, title, price,  time, terms} = values
    const {hour, min, non} = dayTime
    setDisable(!(content &&  title && price  &&  time && hour &&  min && non && terms))
  }, [values ]);

  //const [terms, setTerms] = useState<boolean>(false)
  const [destAddress, setDestAddress] = useState<boolean>(false)

  const handleCheckedChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const {name, checked} = event.target
    if(name === "destAddress"){
      checked? setDestAddress(true) : setDestAddress(false)
    }else {
      checked? setValues({...values, "terms": true})
      : setValues({...values, "terms": false})
    }
  };
  

  // image upload
  const [pictures, setPictures] = useState<any[]>([]);
  const [key, setKey] = useState<any>({});

  const onDrop = async (picture: any) => {
    setPictures(picture);
  };

  return (
    <React.Fragment>
          <AppHomeLayout title='RA2 Serverless'>
          <CssBaseline />
      <main className={classes.layout}>
        <Paper className={classes.paper}>

      <Typography variant="h6" gutterBottom >
      Job Details
      </Typography>
      <Grid container spacing={1}>
      <Grid item xs={12} sm={6}>
          <TextField
            required
            id="title"
            name="title"
            label="Title"
            fullWidth
            onChange={handleInputChange}
            value={values.title}
            autoComplete="title"
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextField
            required
            id="price"
            name="price"
            label="Price"
            fullWidth
            onChange={handleInputChange}
            value={values.price}
            autoComplete="price"
          />
        </Grid>

        <Grid item xs={12} sm={6}>
          <LocationSearchInput label = "Start Location" googleAddressReturn={sourceGoogle}
         />
          </Grid>

          <Grid item xs={12}>

<FormControlLabel
control={
<Tooltip title="Delete">

<Checkbox
checked={destAddress}
onChange={handleCheckedChange}
name="destAddress"
color="primary"
/>
</Tooltip>

}
label="Do you have different End Location"
/>
</Grid>

         {
destAddress?
<Grid item xs={12} sm={6}>
          <LocationSearchInput label = "Destination/End Location" googleAddressReturn={destGoogle}
         />
          </Grid> : null
         } 

        <Grid item xs={5}>
        {/* <DateTimePicker
           name= "Job Date"
          onChange={onChange}
          value={values.time}
        /> */}
<label >Date:</label>
        <input
    type="date"
    className={classes.date}
    name="time"
    id="time"
    min={new Date().toISOString().slice(0,10)}
    value={values.time} 
    onChange={handleInputChange}
    />

        </Grid>

        <Grid  item xs={7}>
        <label >Time:</label>
        <span className={classes.time}>
        <FormControl required>
        <Select
          native
         value={dayTime.hour}
         onChange={handleDayTimeChange}
          name="hour"
          id="hour"
          inputProps={{
            id: 'hour-native-required',
          }}
        >
          <option aria-label="None" value="" />
          <option value={1}>1</option>
          <option value={2}>2</option>
          <option value={3}>3</option>
          <option value={4}>4</option>
          <option value={5}>5</option>
          <option value={6}>6</option>
          <option value={7}>7</option>
          <option value={8}>8</option>
          <option value={9}>9</option>
          <option value={10}>10</option>
          <option value={11}>11</option>
          <option value={12}>12</option>
        </Select>
      </FormControl>

        <FormControl required>
        <Select
          native
          value={dayTime.min}
          onChange={handleDayTimeChange}
           name="min"
           id="min"
          inputProps={{
            id: 'min-native-required',
          }}
        >
          <option aria-label="None" value="" />
          <option value={"00"}>00</option>
          <option value={"15"}>15</option>
          <option value={"30"}>30</option>
          <option value={"45"}>45</option>
        </Select>
      </FormControl>
      <FormControl required>
        <Select
          native
          value={dayTime.non}
          onChange={handleDayTimeChange}
           name="non"
           id="non"
          inputProps={{
            id: 'non-native-required',
          }}
        >
          <option aria-label="None" value="" />
          <option value={"AM"}>AM</option>
          <option value={"PM"}>PM</option>
        </Select>
      </FormControl>
      </span>
        </Grid>


        <Grid item xs={12}>
        <TextField
          multiline
          rows="6"
          required
          id="content"
          name="content"
          label="content"
          fullWidth
          onChange={handleInputChange}
          value={values.content}
          autoComplete="content"
        />
        </Grid>

        <Grid item xs={12}>
        <ImageUploader
      withPreview={true}
      withIcon={true}
      onChange={onDrop}
      imgExtension={[".jpg", ".gif", ".png", ".gif"]}
      maxFileSize={5242880}
    />
            </Grid>
            <Grid item xs={12}>

            <FormControlLabel
        control={
          <Tooltip title="Delete">

          <Checkbox
            checked={values.terms}
            onChange={handleCheckedChange}
            name="checkedB"
            color="primary"
          />
          </Tooltip>

        }
        label="Terms of Service"
      />
            </Grid>
        <Grid
  container
  direction="row"
  justify="flex-end"
  alignItems="center"
>

<Button disabled= {disable} variant="contained"
  color="primary"
  className={classes.button}
onClick={createNewTicket}>Create Request</Button>

</Grid>
      </Grid>

      </Paper>
      </main>

      </AppHomeLayout>
    </React.Fragment>
  );
}

export default CreateTicket
